<template>
  <div id="graph"></div>
</template>

<script>
import * as d3 from "d3";
import { mapGetters } from "vuex";

export default {
  name: "Graph",

  computed: {
    ...mapGetters(["graph", "timerange"]),

    width() {
      return parseFloat(d3.select("#main-wrapper").style("width")) * 0.85;
    },

    height() {
      return (parseFloat(d3.select("#main-wrapper").style("height")) / 3) * 0.85;
    },
    
  },

  watch: {
    timerange() {
      d3.select("#links").remove();
      if (this.timerange[0] != 0) {
        this.drawGraph(this.timerange);
      }
    },
  },

  methods: {
    drawGraph(timerange) {
      d3.select("#graph").append("svg").attr("id", "links");

      var margin = { top: 10, right: 10, bottom: 10, left: 10 },
        width = this.width - margin.left - margin.right,
        height = this.height - margin.top - margin.bottom;

      var svg = d3
        .select("#links")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom);

      var graph = this.graph;

      var nn = graph.nodes.filter(function (d) {
          for (let i = 0; i < d.datetime.length; i++) {
            if (
              d.datetime[i] <= timerange[1] &&
              d.datetime[i] >= timerange[0]
            ) {
              return true;
            }
          }
        });

      var ll = graph.links.filter(function (d) {
          for (let i = 0; i < d.datetime.length; i++) {
            if (
              d.datetime[i] <= timerange[1] &&
              d.datetime[i] >= timerange[0]
            ) {
              return true;
            }
          }
        });

      var max_a = d3.max(graph.links, (d) => d.datetime.length);
      var min_a = d3.min(graph.links, (d) => d.datetime.length);
      var scaler_force = d3
        .scaleLinear()
        .domain([max_a, min_a])
        .range([250, 70]);

      // define simulation
      var simulation = d3
        .forceSimulation(graph.nodes)
        .force(
          "link",
          d3.forceLink(graph.links).id(function (d) {
            return d.id;
          })
        )
        .force(
          "link",
          d3.forceLink(graph.links).distance(function (d) {
            return scaler_force(d.datetime.length);
          })
        )
        .force("charge", d3.forceManyBody().strength(-2)) //图的大小!!
        .force("center", d3.forceCenter(width / 2, height / 2));

      var color = {
        PERSON: "#e56656",
        ORG: "#6390c5",
        GPE: "#6fc7c8",
        TAG: "#ffb53d",
      };

      //link
      var link = svg
        .append("g")
        .attr("class", "links")
        .selectAll("line")
        .data(ll)
        .join("line")
        .attr("stroke-width", function (d) {
          return Math.sqrt(d.datetime.length) / 3;
        })
        

      var linkedByIndex = {};
      ll.forEach(function (d) {
        linkedByIndex[d.source.id + "," + d.target.id] = 1;
        linkedByIndex[d.target.id + "," + d.source.id] = 1;
      });

      // check the dictionary to see if nodes are linked
      function isConnected(a, b) {
        return linkedByIndex[a.id + "," + b.id] || a.id == b.id;
      }

      var scaler = d3.scaleLinear().domain([1, 670]).range([5, 30]);

      var node = svg
        .append("g")
        .attr("class", "nodes")
        .selectAll("g")
        .data(nn)
        .enter()
        .append("g")


      var circles = node
        .append("circle")
        .attr("r", function (d) {
          return scaler(d.datetime.length);
        })
        .attr("fill", function (d) {
          return color[d.group];
        })
        .call(drag(simulation))
        .on("mouseover", function (event, d) {
          circles.style("stroke-opacity", function (o) {
            return isConnected(d, o) ? 1 : 0.2;
          });
          circles.style("fill-opacity", function (o) {
            return isConnected(d, o) ? 1 : 0.2;
          });
          lables.style("fill-opacity", function (o) {
            return isConnected(d, o) ? 1 : 0;
          });
          link.style("stroke-opacity", function (o) {
            return o.source === d || o.target === d ? 1 : 0.2;
          });
          link.style("stroke", function (o) {
            return o.source === d || o.target === d ? o.source.colour : "#ddd";
          });
        })
        .on("mouseout", function () {
          circles.style("stroke-opacity", 1);
          circles.style("fill-opacity", 1);
          lables.style("fill-opacity", function (d) {
            return (d.degree > 10) ? 1 : 0;
          });
          circles.attr("r", function (d) {
            return scaler(d.datetime.length);
          });
          link.style("stroke-opacity", 1);
          link.style("stroke", "#ddd");
        });
   
        

      simulation.on("tick", () => {
        link
          .attr("x1", function (d) {
            return d.source.x;
          })
          .attr("y1", function (d) {
            return d.source.y;
          })
          .attr("x2", function (d) {
            return d.target.x;
          })
          .attr("y2", function (d) {
            return d.target.y;
          });

        node.attr("transform", function (d) {
          return "translate(" + d.x + "," + d.y + ")";
        });
      });

      // 文字标签
      var lables = node
        .append("text")
        .text(function (d) {
          // if (d.degree > 5 || d.degree < 1) {
          return d.name;
          // }
        })
        .style("fill-opacity", function (d) {
          return d.degree > 10 ? 1 : 0;
        })
        .attr("x", 6)
        .attr("y", 3);

      // legend
      svg
        .selectAll(".mydots")
        .data(Object.keys(color))
        .enter()
        .append("circle")
        .attr("cx", 0.01 * width)
        .attr("cy", function (d, i) {
          return 0.02 * height + i * 25;
        }) // 100 is where the first dot appears. 25 is the distance between dots
        .attr("r", 7)
        .style("fill", function (d) {
          return color[d];
        });

      // Add one dot in the legend for each name.
      svg
        .selectAll(".mylabels")
        .data(Object.keys(color))
        .enter()
        .append("text")
        .attr("x", 0.01 * width + 20)
        .attr("y", function (d, i) {
          return 0.02 * height + i * 25;
        }) // 100 is where the first dot appears. 25 is the distance between dots
        .style("fill", function (d) {
          return color[d];
        })
        .text(function (d) {
          if (d == "PERSON") {
            return "Person";
          } else if (d == "ORG") {
            return "Organization";
          } else if (d == "GPE") {
            return "Location";
          } else {
            return "Tag";
          }
        })
        .attr("text-anchor", "left")
        .style("alignment-baseline", "middle");

      //drag
      function drag(simulation) {
        function dragstarted(event, d) {
          if (!event.active) simulation.alphaTarget(0.1).restart();
          d.fx = d.x;
          d.fy = d.y;
        }

        function dragged(event, d) {
          d.fx = event.x;
          d.fy = event.y;
        }

        function dragended(event, d) {
          if (!event.active) simulation.alphaTarget(0);
          d.fx = null;
          d.fy = null;
        }

        return d3
          .drag()
          .on("start", dragstarted)
          .on("drag", dragged)
          .on("end", dragended);
      }
    },
  },
};
</script>

<style>
#graph {
  margin: auto;
}

.links line {
  stroke: rgb(99, 99, 99);
  stroke-opacity: 0.6;
}

.nodes circle {
  stroke: #fff;
  stroke-width: 1.5px;
}

text {
  font-family: sans-serif;
  font-size: 10px;
}

svg {
  border: 0px solid gray;
}

g.am-axis text {
  font-size: 6px;
}

.domain {
  fill: none;
}

.tick > line {
  stroke: black;
  stroke-width: 1px;
  stroke-opacity: 0.25;
}
</style>
