<template>
  <div id="wordcloud"></div>
</template>

<script>
import * as d3 from "d3";
import myCloud from "../assets/myCloud.js";
import { mapGetters } from "vuex";

export default {
  name: "WordCloud",

  computed: {
    ...mapGetters(["words"]),

    width() {
      return parseFloat(d3.select("#main-wrapper").style("width")) * 0.35*0.9;
    },

    height() {
      return (parseFloat(d3.select("#main-wrapper").style("height")) / 3) * 0.35*0.9;
    },
  },

  methods: {
    getWordCloud(wordList) {
      let wordOption = {
        wordList,
        size: [this.width, this.height], // 盒子的宽高
        font: "Arial",
        padding: 3,
        svg:"#wordcloud",
      };
      myCloud(wordOption);
    },

    tfidf(allwords) {
      var counts = {};
      var keys = [];
      var tokens = allwords[0].split(/\W+/);
      for (var i = 0; i < tokens.length; i++) {
        var word = tokens[i].toLowerCase();
        if (counts[word] === undefined) {
          counts[word] = {
            tf: 1,
            df: 1,
          };
          keys.push(word);
        } else {
          counts[word].tf = counts[word].tf + 1;
        }
      }

      var othercounts = [];
      for (var j = 1; j < allwords.length; j++) {
        var tempcounts = {};
        var tokens_2 = allwords[j].split(/\W+/);
        for (var k = 0; k < tokens_2.length; k++) {
          var w = tokens_2[k].toLowerCase();
          if (tempcounts[w] === undefined) {
            tempcounts[w] = true;
          }
        }
        othercounts.push(tempcounts);
      }

      for (var a = 0; a < keys.length; a++) {
        var word_2 = keys[a];
        for (var b = 0; b < othercounts.length; b++) {
          var tempcounts_2 = othercounts[b];
          if (tempcounts_2[word_2]) {
            counts[word_2].df++;
          }
        }
      }

      for (var c = 0; c < keys.length; c++) {
        var word_3 = keys[c];
        var wordobj = counts[word_3];
        wordobj.tfidf =
          (wordobj.tf / allwords[0].split(" ").length) *
          (Math.log(allwords.length / wordobj.df) + 1);
      }
      return counts;
    },
  },

  watch: {
    words() {
      d3.select("#cloudsvg").remove();
      // console.log(this.words);
      if (this.words["in"] == "" && this.words["out"] == "") {
        return;
      }
      var output = this.tfidf([this.words["in"], this.words["out"]]);
      // console.log(output);
      var min_value = 99;
      var max_value = -99;
      var wordList = [];
      Object.keys(output).forEach(function (d) {
        let v = output[d].tfidf;
        if (v < min_value) {
          min_value = v;
        }
        if (v > max_value) {
          max_value = v;
        }
      });
      var scale = d3
        .scaleLinear()
        .domain([min_value, max_value])
        .range([this.width * 0.03, this.width * 0.2]);
      Object.keys(output).forEach(function (d) {
        wordList.push({ text: d, size: scale(output[d].tfidf) });
      });
      this.getWordCloud(wordList);
    },
  },
};
</script>

<style scoped>
#wordcloud {
  margin: auto;
}
</style>
