<template>
  <div id="race"></div>
</template>


<script>
import { mapGetters } from "vuex";
import * as d3 from "d3";

export default {
  name: "Race",

  computed: {
    ...mapGetters(["yuheng", "timerange", "events"]),

    width() {
      return parseFloat(d3.select("#main-wrapper").style("width")) * 0.65;
    },

    height() {
      return (
        (parseFloat(d3.select("#main-wrapper").style("height")) / 3) * 0.35
      );
    },
  },

  watch: {
    yuheng() {
      this.drawRace(this.yuheng, this.events, this.timerange);
    },

    timerange() {
      this.drawRace(this.yuheng, this.events, this.timerange);
    },
  },

  methods: {
    drawRace(data, events, time) {
      d3.selectAll(".raceSVG").remove();

      // 确定时间范围
      var timeMap = d3
        .scaleLinear()
        .range([1390496400000, 1390512840000])
        .domain([61200, 77685]);

      if (time[0] == 0) {
        var timerange = [1390496400000, 1390512840000];
      } else {
        timerange = [timeMap(time[0]), timeMap(time[1])];
      }

      // 确定关键词
      if (Object.keys(events).length == 0) {
        var keys = {
          Danger: ["kidnap", "terrorist", "police", "cop", "gun", "shoot"],
          Fire: ["fire", "dolphin", "dancing", "building", "apartment"],
          "Black-van": ["incident", "traffic", "van"],
          Rally: ["pok", "rally", "protest"],
          Concert: ["victor", "song"],
        };
      } else {
        keys = events;
      }

      // 传入数据
      var data_1 = data;

      var svg = d3
        .select("#race")
        .append("svg")
        .attr("class", "raceSVG")
        .attr("width", this.width)
        .attr("height", this.height);

      var tickDuration = 2500; //控制播放速度

      var top_n = 11;
      var height = this.height;
      var width = this.width;

      const margin = {
        top: 40,
        right: 50,
        bottom: 0,
        left: 50,
      };

      let barPadding = (height - (margin.bottom + margin.top)) / (top_n * 5);

      // svg
      //   .append("text")
      //   .attr("class", "title")
      //   .attr("y", 24)
      //   .attr("x", 50)
      //   .html("Occurrences of keywords");

      var bin = 300000; // 控制时间间隔

      /////////////////////处理数据
      var nl = ["time"];
      for (let i = 0; i < Object.keys(keys).length; i++) {
        nl = nl.concat(keys[Object.keys(keys)[i]]);
      }
      var new_data = [nl];
      let year = timerange[0];

      var index = 0;
      var lastvalue = 0;

      for (var t = timerange[0]; t < timerange[1]; t = t + bin) {
        var ll = [];
        for (let k = 0; k < nl.length - 1; k++) {
          var count = 0;
          if (k == nl.length - 2) {
            for (let i = index; i < data_1.length; i++) {
              index += 1;
              if (data_1[i][0] >= t && data_1[i][0] < t + bin) {
                if (data_1[i][1].includes(nl[k + 1])) {
                  count += 1;
                }
              } else if (data_1[i][0] > t + bin) {
                break;
              }
            }
          } else {
            for (let i = index; i < data_1.length; i++) {
              if (data_1[i][0] >= t && data_1[i][0] < t + bin) {
                if (data_1[i][1].includes(nl[k + 1])) {
                  count += 1;
                }
              } else if (data_1[i][0] > t + bin) {
                break;
              }
            }
          }
          ll.push(count);
        }
        var tmp = [t].concat(ll);
        new_data.push(tmp);
      }
      var group = "";
      var data_for_bar = [["date", "name", "category", "value", "lastValue"]];
      for (let i = 1; i < new_data.length; i++) {
        for (let j = 1; j < nl.length; j++) {
          for (let k = 0; k < Object.keys(keys).length; k++) {
            if (Object.values(keys)[k].includes(nl[j])) {
              group = Object.keys(keys)[k];
            }
          }
          if (i == 1) {
            data_for_bar.push([
              [new_data[i][0], nl[j], group, new_data[i][j], 0],
            ]);
          } else {
            lastvalue = new_data[i - 1][j];
            data_for_bar.push([
              [new_data[i][0], nl[j], group, new_data[i][j], lastvalue],
            ]);
          }
        }
      }
      let csvContent1 = new String();

      data_for_bar.forEach(function (rowArray) {
        let row = rowArray.join(",");
        csvContent1 += row + "\r\n";
      });

      var data = d3.csvParse(csvContent1);

      const scale = d3.scaleOrdinal(d3.schemeTableau10);
      if (data.some((d) => d.category !== undefined)) {
        const categoryByName = new Map(data.map((d) => [d.name, d.category]));
        scale.domain(categoryByName.values());
      }
      data.forEach((d) => {
        d.value = +d.value;
        d.category = d.category;
        (d.lastValue = d.lastValue),
          (d.value = isNaN(d.value) ? 0 : d.value),
          (d.date = d.date),
          (d.colour = scale(d.category));
      });

      let yearSlice = data
        .filter((d) => d.date == year && !isNaN(d.value))
        .sort((a, b) => b.value - a.value)
        .slice(0, top_n);

      yearSlice.forEach((d, i) => (d.rank = i));
      function negative(n) {
        return n < 0;
      }

      let x = d3
        .scaleLinear()
        .domain([0, d3.max(yearSlice, (d) => d.value)])
        .range([margin.left, width - margin.right]); /////调宽了

      let y = d3
        .scaleLinear()
        .domain([top_n, 0])
        .range([height - margin.bottom, margin.top]);

      let xAxis = d3
        .axisTop()
        .scale(x)
        .ticks(width > 500 ? 5 : 2)
        .tickSize(-(height - margin.top - margin.bottom))
        .tickFormat((d) => d3.format(",")(d));

      let formatDate = d3.utcFormat("%a %b %e %X %Y");

      svg
        .append("g")
        .attr("class", "axis xAxis")
        .attr("transform", `translate(0, ${margin.top})`)
        .call(xAxis)
        .selectAll(".tick line")
        .classed("origin", (d) => d == 0);

      svg
        .selectAll("rect.bar")
        .data(yearSlice, (d) => d.name)
        .enter()
        .append("rect")
        .attr("class", "bar")
        .attr("x", x(0) + 1)
        .attr("width", (d) =>
          negative(x(d.value) - x(0) - 1) ? 0 : x(d.value) - x(0) - 1
        )

        // .attr('width', d => x(d.value)-x(0)-1)
        .attr("y", (d) => y(d.rank) + 5)
        .attr("height", y(1) - y(0) - barPadding)
        .style("fill", (d) => d.colour);

      svg
        .selectAll("text.label")
        .data(yearSlice, (d) => d.name)
        .enter()
        .append("text")
        .attr("class", "label")
        .attr("x", (d) => x(d.value) - 8)
        .attr("y", (d) => y(d.rank) + 5 + (y(1) - y(0)) / 2 + 1)
        .style("text-anchor", "end")
        .html((d) => d.name);

      svg
        .selectAll("text.valueLabel")
        .data(yearSlice, (d) => d.name)
        .enter()
        .append("text")
        .attr("class", "valueLabel")
        .attr("x", (d) => x(d.value) + 5)
        .attr("y", (d) => y(d.rank) + 5 + (y(1) - y(0)) / 2 + 1)
        .text((d) => d.value);

      const halo = function (text, strokeWidth) {
        text
          .select(function () {
            return this.parentNode.insertBefore(this.cloneNode(true), this);
          })
          .style("fill", "#ffffff")
          .style("stroke", "#ffffff")
          .style("stroke-width", strokeWidth)
          .style("stroke-linejoin", "round")
          .style("opacity", 1);
      };

      // 右下角时间戳
      let yearText = svg
        .append("text")
        .attr("class", "yearText")
        .attr("x", width - margin.right)
        .attr("y", height)
        .style("text-anchor", "end")
        .style("font-size", 15)
        .html(formatDate(year))
        .call(halo, 10);

      ///////////////////////////////tick start///////////////////////////////////////////////////
      let ticker = d3.interval(() => {
        // 数据帧 yearslice
        yearSlice = data
          .filter((d) => d.date == year && !isNaN(d.value))
          .sort((a, b) => b.value - a.value)
          .slice(0, top_n);

        yearSlice.forEach((d, i) => (d.rank = i));

        // x映射
        x.domain([0, d3.max(yearSlice, (d) => d.value)]);

        svg
          .select(".xAxis")
          .transition()
          .duration(tickDuration)
          .ease(d3.easeLinear)
          .call(xAxis);

        // bars: 柱子们
        let bars = svg.selectAll(".bar").data(yearSlice, (d) => d.name);

        bars
          .enter()
          .append("rect")
          .attr("class", (d) => `bar ${d.name.replace(/\s/g, "_")}`)
          .attr("x", x(0) + 1)
          .attr("width", (d) =>
            negative(x(d.value) - x(0) - 1) ? 0 : x(d.value) - x(0) - 1
          )
          .attr("y", () => y(top_n + 1) + 5)
          .attr("height", () =>
            negative(y(1) - y(0) - barPadding) ? 0 : y(1) - y(0) - barPadding
          )
          .style("fill", (d) => d.colour)
          .transition()
          .duration(tickDuration)
          .ease(d3.easeLinear)
          .attr("y", (d) => y(d.rank) + 5);

        bars
          .transition()
          .duration(tickDuration)
          .ease(d3.easeLinear)
          .attr("width", (d) =>
            negative(x(d.value) - x(0) - 1) ? 0 : x(d.value) - x(0) - 1
          )
          .attr("y", (d) => y(d.rank) + 5);

        bars
          .exit()
          .transition()
          .duration(tickDuration)
          .ease(d3.easeLinear)
          .attr("width", (d) =>
            negative(x(d.value) - x(0) - 1) ? 0 : x(d.value) - x(0) - 1
          )
          .attr("y", () => y(top_n + 1) + 5)
          .remove();

        let labels = svg.selectAll(".label").data(yearSlice, (d) => d.name);

        labels
          .enter()
          .append("text")
          .attr("class", "label")
          .attr("x", (d) => x(d.value) - 8)
          .attr("y", () => y(top_n + 1) + 5 + (y(1) - y(0)) / 2)
          .style("text-anchor", "end")
          .html((d) => d.name)
          .transition()
          .duration(tickDuration)
          .ease(d3.easeLinear)
          .attr("y", (d) => y(d.rank) + 5 + (y(1) - y(0)) / 2 + 1);

        labels
          .transition()
          .duration(tickDuration)
          .ease(d3.easeLinear)
          .attr("x", (d) => x(d.value) - 8)
          .attr("y", (d) => y(d.rank) + 5 + (y(1) - y(0)) / 2 + 1);

        labels
          .exit()
          .transition()
          .duration(tickDuration)
          .ease(d3.easeLinear)
          .attr("x", (d) => x(d.value) - 8)
          .attr("y", () => y(top_n + 1) + 5)
          .remove();

        let valueLabels = svg
          .selectAll(".valueLabel")
          .data(yearSlice, (d) => d.name);
        var formatNumber = d3.format(",d");
        function textTween(a, b) {
          const i = d3.interpolateNumber(a, b);
          return function (t) {
            this.textContent = formatNumber(i(t));
          };
        }
        valueLabels
          .enter()
          .append("text")
          .attr("class", "valueLabel")
          .attr("x", (d) => x(d.value) + 5)
          .attr("y", () => y(top_n + 1) + 5)
          .text((d) => d.value)
          .transition()
          .duration(tickDuration)
          .ease(d3.easeLinear)
          .attr("y", (d) => y(d.rank) + 5 + (y(1) - y(0)) / 2 + 1);

        valueLabels
          .transition()
          .duration(tickDuration)
          .ease(d3.easeLinear)
          .attr("x", (d) => x(d.value) + 5)
          .attr("y", (d) => y(d.rank) + 5 + (y(1) - y(0)) / 2 + 1)
          .tween("text", (d) => textTween(d.lastValue, d.value));

        valueLabels
          .exit()
          .transition()
          .duration(tickDuration)
          .ease(d3.easeLinear)
          .attr("x", (d) => x(d.value) + 5)
          .attr("y", () => y(top_n + 1) + 5)
          .remove();

        yearText.html(formatDate(year));

        year += bin;

        if (year >= timerange[1]) {
          //   ticker.stop();
          year = timerange[0];
        }
      }, tickDuration);
    },
  },
};
</script>

<style>
/* text {
  font-size: 16px;
  font-family: Open Sans, sans-serif;
} */
text.title {
  font-size: 24px;
  font-weight: 500;
}
text.subTitle {
  font-weight: 500;
  fill: #777777;
}
text.caption {
  font-weight: 400;
  font-size: 14px;
  fill: #777777;
}
text.label {
  font-weight: 600;
}

text.valueLabel {
  font-weight: 300;
}

text.yearText {
  font-size: 64px;
  font-weight: 700;
  opacity: 0.25;
}
.tick text {
  fill: #777777;
}
.xAxis .tick:nth-child(2) text {
  text-anchor: start;
}
.tick line {
  shape-rendering: CrispEdges;
  stroke: #dddddd;
}
.tick line.origin {
  stroke: #aaaaaa;
}
path.domain {
  display: none;
}
</style>