<template>
  <div id="container_up">
    <div class="wrapper">
      <div>
        Relevant {{ "( " + Object.keys(relative).length + " / " + stat[0] + " )" }}
      </div>
      <div id="left">
        <MessageCard
          v-for="text in relative"
          :key="text.ID"
          :TEXT="text"
          :TYPE="1"
          :SENTIMENT="text.polarity"
          :Subjectivity="text.subjectivity"
          :ID="text.ID"
        />
      </div>
      <div id="cloud_relative"></div>
    </div>

    <el-divider class="divider" direction="vertical"></el-divider>

    <div class="wrapper">
      <div>
        Chatter {{ "( " + Object.keys(chatter).length + " / " + stat[1] + " )" }}
      </div>
      <div id="middle">
        <MessageCard
          v-for="text in chatter"
          :key="text.ID"
          :TEXT="text"
          :TYPE="2"
          :SENTIMENT="text.polarity"
          :Subjectivity="text.subjectivity"
          :ID="text.ID"
        />
      </div>
      <div id="cloud_chatter"></div>
    </div>

    <el-divider class="divider" direction="vertical"></el-divider>

    <div class="wrapper">
      <div>Spam {{ "( " + Object.keys(spam).length + " / " + stat[2] + " )" }}</div>
      <div id="right">
        <MessageCard
          v-for="text in spam"
          :key="text.ID"
          :TEXT="text"
          :TYPE="3"
          :SENTIMENT="text.polarity"
          :Subjectivity="text.subjectivity"
          :ID="text.ID"
        />
      </div>
      <div id="cloud_spam"></div>
    </div>
  </div>

  <!-- <div id="container_down">
    <div id="cloud_relative"></div>

    <el-divider class="divider" direction="vertical"></el-divider>

    <div id="cloud_chatter"></div>

    <el-divider class="divider" direction="vertical"></el-divider>

    <div id="cloud_spam"></div>
  </div> -->
</template>

<script>
import { mapGetters } from "vuex";
import MessageCard from "./MessageCard.vue";
import * as d3 from "d3";
import myCloud from "../assets/myCloud.js";

export default {
  name: "Text",

  components: { MessageCard },

  data() {
    return {
      stat: [0, 0, 0],
    };
  },

  computed: {
    ...mapGetters(["dataset", "relative", "chatter", "spam", "timerange"]),

    width() {
      return parseFloat(d3.select("#cloud_relative").style("width")) * 0.9;
    },

    height() {
      return parseFloat(d3.select("#cloud_relative").style("height")) * 0.9;
    },
  },

  watch: {
    relative() {
      d3.selectAll(".cloudsvg").remove();
      if (this.timerange[0] == 0) {
        return;
      }
      this.getWordCloud(this.getWordList(this.relative), "#cloud_relative");
      this.getWordCloud(this.getWordList(this.chatter), "#cloud_chatter");
      this.getWordCloud(this.getWordList(this.spam), "#cloud_spam");
    },

    dataset() {
      var a = 0,
        b = 0,
        c = 0;
      this.dataset.forEach(function (d) {
        if (d.label == 1) {
          a++;
        } else if (d.label == 2) {
          b++;
        } else {
          c++;
        }
      });
      this.stat = [a, b, c];
    },
  },

  methods: {
    getWordList(data) {
      let s = "";
      data.forEach(function (d) {
        s = s + d.words + " ";
      });

      var counts = {};
      var tokens = s.split(/\W+/);
      for (var i = 0; i < tokens.length; i++) {
        var word = tokens[i].toLowerCase();
        if (counts[word] === undefined) {
          counts[word] = 1;
        } else {
          counts[word] += 1;
        }
      }
      var min_value = 99;
      var max_value = -99;
      var wordList = [];
      Object.keys(counts).forEach(function (d) {
        let v = counts[d];
        if (v < min_value) {
          min_value = v;
        }
        if (v > max_value) {
          max_value = v;
        }
      });
      var scale = d3
        .scaleLinear()
        .domain([min_value, max_value])
        .range([this.width * 0.03, this.width * 0.2]);
      Object.keys(counts).forEach(function (d) {
        wordList.push({ text: d, size: scale(counts[d]) });
      });
      return wordList;
    },

    getWordCloud(wordList, svg) {
      let wordOption = {
        wordList,
        size: [this.width, this.height], // 盒子的宽高
        font: "Arial",
        padding: 3,
        svg: svg,
      };
      myCloud(wordOption);
    },
  },
};
</script>

<style scoped>
#container_up {
  display: flex;
  height: 100%;
  justify-content: space-around;
}

#container_down {
  display: flex;
  height: 30%;
  justify-content: space-around;
}

.wrapper {
  height: 100%;
  width: 30%;
}

#left,
#middle,
#right {
  height: 55%;
  width: 100%;
  background-color: white;
  overflow-y: auto;
  border: 1px solid silver;
  border-radius: 3%;
  margin-top: 5%;
}

#cloud_relative,
#cloud_chatter,
#cloud_spam {
  height: 30%;
  width: 100%;
  background-color: white;
  overflow-y: auto;
  border: 1px solid silver;
  border-radius: 3%;
  margin-top: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.divider {
  height: 95%;
}

.box-card {
  width: 80%;
  margin: auto;
  margin-bottom: 5px;
  margin-top: 5px;
}

.author {
  color: steelblue;
}

.tag {
  color: rgb(255, 127, 85);
}
</style>