import { createStore } from 'vuex'
import * as d3 from "d3"

export default createStore({
  state: {
    dataset: [],
    yuheng: [],
    map: [],
    location: [],
    graph: [],
    race: [],
    timerange: [0, 0],
    focus: [],
    words: { in: "", out: "" },
    labels: { author: [], tag: [], keyword: [], spam_author: [], spam_tag: [], spam_keyword: [] },
    textID: { to_relative: [], to_chatter: [], to_spam: [] },
    events: {}
  },

  getters: { /* Define how different views access the global data. */

    dataset(state) {
      return state.dataset
    },

    relevant(state) {
      return state.dataset.filter((d) => (d.label == 1))
    },

    yuheng(state) {
      return state.yuheng
    },

    map(state) {
      return state.map
    },

    location(state) {
      return state.location
    },

    graph(state) {
      return state.graph;
    },

    race(state) {
      return state.race;
    },

    focus(state) {
      return state.focus;
    },

    labels(state) {
      return state.labels;
    },

    timerange(state) {
      return state.timerange
    },

    relative(state) {
      return state.focus.filter((d) => (d.label == 1 || d.type == "ccdata"))
    },

    chatter(state) {
      return state.focus.filter((d) => (d.label == 2))
    },

    spam(state) {
      return state.focus.filter((d) => (d.label == 3))
    },

    points(state) {
      return state.focus.filter((d) => (d.latitude !== null))
    },

    words(state) {
      if (state.timerange[0] == 0) {
        return { in: "", out: "" }
      }
      else {
        var str_in = "";
        var str_out = "";
        state.focus.forEach(function (d) {
          if (d.label == 1) {
            str_in = str_in + d.words + " "
          } else {
            str_out = str_out + d.words + " "
          }
        })
        return { in: str_in, out: str_out }
      }
    },

    events(state) {
      return state.events
    },

    timeline_Type(state) {
      var data = [];
      var ticks = 250;
      var hists = [];
      for (let i = 1; i <= ticks; i++) {
        hists.push((16485 / ticks) * i + 61200);
      }

      var mbdata = 0;
      var ccdata = 0;
      var i = 0;

      state.dataset.forEach(function (d) {
        var pivot = hists[i];

        if (d.datetime < pivot) {
          if (d.type == "mbdata") {
            mbdata++;
          } else {
            ccdata++;
          }
        } else {
          data.push({ no: i, mbdata: mbdata, ccdata: ccdata * 5 })
          mbdata = 0;
          ccdata = 0;
          i++;
        }
      })
      return data
    },

    timeline_Category(state) {
      var data = [];
      var ticks = 250;
      var hists = [];
      for (let i = 1; i <= ticks; i++) {
        hists.push((16485 / ticks) * i + 61200);
      }

      var relevant = 0;
      var spam = 0;
      var chatter = 0;
      var i = 0;

      state.dataset.forEach(function (d) {
        var pivot = hists[i];

        if (d.datetime < pivot) {
          if (d.label == 1) {
            relevant++;
          } else if (d.label == 2) {
            chatter++
          } else {
            spam++;
          }
        } else {
          data.push({ no: i, relevant: relevant, chatter: chatter, spam: spam })
          // data.push({ no: i, spam: spam, chatter: chatter, relevant: relevant })
          relevant = 0;
          spam = 0;
          chatter = 0;
          i++;
        }
      })
      return data

    },

    timeline_Event(state) {
      var data = state.yuheng
      var bin = 100000
      if (Object.keys(state.events).length == 0) {
        var keys = { 'Danger': ['terrorist', 'police', 'cop', 'gun', 'shoot'], 'Fire': ['fire', 'building', 'apartment'], 'Black-van': ['incident', 'traffic', 'van'], 'Rally': ['pok', 'rally', 'protest'], 'Concert': ['victor', 'song'] }
      } else {
        keys = state.events
      }
      var nl = ['no']
      for (let i = 0; i < Object.keys(keys).length; i++) {
        nl = nl.concat(keys[Object.keys(keys)[i]])
      }
      var new_data = [nl]
      var timerange = [1390496400000, 1390512840000]
      var index = 0

      for (var t = timerange[0]; t < timerange[1]; t = t + bin) {
        var ll = []
        for (let k = 0; k < nl.length - 1; k++) {
          var count = 0
          if (k == nl.length - 2) {
            for (let i = index; i < data.length; i++) {
              index += 1
              if (data[i][0] >= t && data[i][0] < t + bin) {
                if (data[i][1].includes(nl[k + 1])) {
                  // console.log(data[i][0],data[i][1])
                  count += 1
                }
              }
              else if (data[i][0] > t + bin) {
                break
              }
            }
          } else {
            for (let i = index; i < data.length; i++) {
              if (data[i][0] >= t && data[i][0] < t + bin) {
                if (data[i][1].includes(nl[k + 1])) {
                  count += 1
                }
              }
              else if (data[i][0] > t + bin) {
                break
              }
            }
          }
          ll.push(count)
        }
        var tmp = [t].concat(ll)
        new_data.push(tmp)
      }

      let csvContent = new String();

      new_data.forEach(function (rowArray) {
        let row = rowArray.join(",");
        csvContent += row + "\r\n";
      });

      var dt = d3.csvParse(csvContent);

      return dt;
    },

    timeline_Sentiment(state) {
      var data = [];
      var ticks = 250;
      var hists = [];
      for (let i = 1; i <= ticks; i++) {
        hists.push((16485 / ticks) * i + 61200);
      }

      var relevant = 0;
      var spam = 0;
      var chatter = 0;
      var con_relevant = 0;
      var con_spam = 0;
      var con_chatter = 0;
      var i = 0;

      state.dataset.forEach(function (d) {
        var pivot = hists[i];
        // if (d.polarity < 0) {
        if (d.datetime < pivot) {
          if (d.label == 1) {
            relevant += (1 - d.polarity);
            con_relevant++;
          } else if (d.label == 2) {
            chatter += (1 - d.polarity);
            con_chatter++;
          } else {
            spam += (1 - d.polarity);
            con_spam++;
          }
        } else {
          if (con_relevant == 0) { var a = 0 } else { a = relevant / con_relevant }
          if (con_chatter == 0) { var b = 0 } else { b = chatter / con_chatter }
          if (con_spam == 0) { var c = 0 } else { c = spam / con_spam }
          data.push({ no: i, relevant: a, chatter: b, spam: c })
          // data.push({ no: i, spam: c, chatter: b, relevant: a })
          relevant = 0;
          spam = 0;
          chatter = 0;
          con_relevant = 0;
          con_spam = 0;
          con_chatter = 0;
          i++;
        }
        // }
      })
      return data

    },

  },



  mutations: { /* Change state value */

    update(state, payload) {
      state[payload.field] = payload.data
    },

    changeFocus(state, bound) {
      state.focus = state.dataset.filter((d) => ((d.datetime <= bound.upper) && (d.datetime >= bound.down)))
    },

    resetFocus(state) {
      state.focus = []
    },

    changeTimeRange(state, bound) {
      state.timerange = bound
    },

    resetTimeRange(state) {
      state.timerange = [0, 0]
    },

    updateLabels(state, order) {
      if (order.operation == "add") {
        state.labels[order.type].push(order.word)
      }
      else {
        state.labels[order.type] = state.labels[order.type].filter(d => d != order.word)
      }
    },

    updateTextID(state, order) {
      if (order.operation == "add") {
        state.textID[order.type].push(order.id)
      }
      else {
        state.textID[order.type] = state.textID[order.type].filter(d => d != order.id)
      }
      // console.log(state.textID)
    },

    updateEvents(state, order) {
      if (order.operation == "add") {
        if (state.events[order.key] == undefined) {
          state.events[order.key] = [order.word]
        } else {
          state.events[order.key].push(order.word)
        }
      }
      else {
        state.events[order.key] = state.events[order.key].filter(d => d != order.word)
        if (state.events[order.key].length == 0) {
          delete state.events[order.key]
        }
      }
    },

    updateData(state) {
      var data = [];

      var keywords = []
      for (let i = 0; i < Object.keys(state.events).length; i++) {
        keywords = keywords.concat(state.events[Object.keys(state.events)[i]])
      }

      // console.log(keywords)

      state.dataset.forEach(function (d) {
        d.highlight = 0

        if (state.labels.author.includes(d.author)) {
          d.label = 1
        } else if (state.labels.spam_author.includes(d.author)) {
          d.label = 3
        }

        eval(d.Tags).forEach(function (t) {
          if (state.labels.tag.includes(t)) {
            d.label = 1
          } else if (state.labels.spam_tag.includes(t)) {
            d.label = 3
          }
        })

        if (state.textID.to_relative.includes(d.ID)) {
          d.label = 1
        } else if (state.textID.to_spam.includes(d.ID)) {
          d.label = 3
        } else if (state.textID.to_chatter.includes(d.ID)) {
          d.label = 2
        }

        keywords.forEach(function (w) {
          if (d.words !== null) {
            if (d.words.includes(w)) {
              d.highlight = 1
              // console.log(d.words)
              // console.log("here")
            }
          }
        })

        data.push(d)
      })
      // console.log("completed")
      state.dataset = data
    }
  },


  actions: {  /* Invoke functions; only functions here can use mutations */
    async getDataset(context) {
      await d3.json('./data.json').then(function (d) {
        context.commit('update', {
          field: 'dataset',
          data: d
        })
        // context.commit('update', {
        //   field: 'focus',
        //   data: d
        // })
      })
    },

    async getMap(context) {
      await d3.json('./map.json').then(function (d) {
        context.commit('update', {
          field: 'map',
          data: d
        })
      })
    },

    async getLocation(context) {
      await d3.json('./location.json').then(function (d) {
        context.commit('update', {
          field: 'location',
          data: d
        })
      })
    },

    async getGraph(context) {
      await d3.json('./graph.json').then(function (d) {
        context.commit('update', {
          field: 'graph',
          data: d
        })
      })
    },

    async Race(context) {
      await d3.json('./race.json').then(function (d) {
        context.commit('update', {
          field: 'race',
          data: d
        })
      })
    },

    async getYuheng(context) {
      await d3.json('./yuheng.json').then(function (d) {
        context.commit('update', {
          field: 'yuheng',
          data: d
        })
      })
    },

    focus(context, bound) {
      context.commit('changeFocus', bound)
    },

    reset(context) {
      context.commit('resetFocus')
      context.commit('resetTimeRange')
    },

    setTimeRange(context, bound) {
      context.commit('changeTimeRange', bound)
    },

    changeLabel(context, order) {
      context.commit('updateLabels', order)
    },

    changeText(context, order) {
      context.commit('updateTextID', order)
    },

    changeEvents(context, order) {
      context.commit('updateEvents', order)
    },

    updateData(context) {
      context.commit('updateData')
    }
  },

  modules: {},
})
