<template>
  <div id="container" style="background-color: whitesmoke">
    <div class="wrap">
      <el-input v-model="event_key" placeholder="Event"></el-input>
      <el-input v-model="event_word" placeholder="Keyword"></el-input>
      <el-button
        @click="addEvent"
        type="warning"
        icon="el-icon-star-on"
        size="mini"
      ></el-button>
    </div>
    <div class="info">
      <template v-for="key in Object.keys(events)" :key="key">
        <el-tag
          closable
          size="mini"
          type="warning"
          @close="removeEvent(key, word)"
          v-for="word in events[key]"
          :key="word"
          >{{ key + ': ' + word }}</el-tag
        >
      </template>
    </div>

    <div class="wrap">
      <el-select v-model="value_1" placeholder="">
        <el-option
          v-for="item in options_1"
          :key="item.value"
          :label="item.label"
          :value="item.value"
          :disabled="item.disabled"
        >
        </el-option>
      </el-select>
      <el-input v-model="input_1" placeholder="input"></el-input>
      <el-button
        @click="addHighlight"
        type="primary"
        icon="el-icon-success"
        size="mini"
      ></el-button>
    </div>
    <div class="labels">
      <el-tag
        closable
        size="mini"
        @close="removeLabel('author', item)"
        v-for="item in labels.author"
        :key="item"
        >{{ "@" + item }}</el-tag
      >
      <el-tag
        closable
        size="mini"
        @close="removeLabel('tag', item)"
        v-for="item in labels.tag"
        :key="item"
        >{{ "#" + item }}</el-tag
      >
      <el-tag
        closable
        size="mini"
        @close="removeLabel('keyword', item)"
        v-for="item in labels.keyword"
        :key="item"
        >{{ item }}</el-tag
      >
    </div>

    <div class="wrap">
      <el-select v-model="value_2" placeholder="">
        <el-option
          v-for="item in options_2"
          :key="item.value"
          :label="item.label"
          :value="item.value"
          :disabled="item.disabled"
        >
        </el-option>
      </el-select>
      <el-input v-model="input_2" placeholder="input"></el-input>
      <el-button
        @click="addScreening"
        type="danger"
        icon="el-icon-error"
        size="mini"
      ></el-button>
    </div>
    <div class="labels">
      <el-tag
        closable
        size="mini"
        type="danger"
        @close="removeLabel('spam_author', item)"
        v-for="item in labels.spam_author"
        :key="item"
        >{{ "@" + item }}</el-tag
      >
      <el-tag
        closable
        size="mini"
        type="danger"
        @close="removeLabel('spam_tag', item)"
        v-for="item in labels.spam_tag"
        :key="item"
        >{{ "#" + item }}</el-tag
      >
      <el-tag
        closable
        size="mini"
        type="danger"
        @close="removeLabel('spam_keyword', item)"
        v-for="item in labels.spam_keyword"
        :key="item"
        >{{ item }}</el-tag
      >
    </div>

    <div class="update">
      <el-button type="primary" @click="updateData">Commit Update!</el-button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { ElMessage } from "element-plus";

export default {
  name: "Keyword",

  computed: {
    ...mapGetters(["labels", "events"]),
  },

  data() {
    return {
      options_1: [
        {
          value: "author",
          label: "Author",
        },
        {
          value: "tag",
          label: "Tag",
        },
        {
          value: "keyword",
          label: "Word",
        },
      ],
      options_2: [
        {
          value: "author",
          label: "Author",
        },
        {
          value: "tag",
          label: "Tag",
        },
        {
          value: "keyword",
          label: "Word",
        },
      ],
      event_key: "",
      event_word: "",
      value_1: "",
      value_2: "",
      input_1: "",
      input_2: "",
    };
  },

  methods: {
    addEvent() {
      if(this.event_key != "" && this.event_word !=""){
      this.$store.dispatch("changeEvents", {
        operation: "add",
        key: this.event_key,
        word: this.event_word,
      });
      this.event_key = "";
      this.event_word = "";
      }
    },

    removeEvent(key, word) {
      this.$store.dispatch("changeEvents", {
        operation: "del",
        key: key,
        word: word,
      });
    },

    addHighlight() {
      this.$store.dispatch("changeLabel", {
        operation: "add",
        type: this.value_1,
        word: this.input_1,
      });
      this.value_1 = "";
      this.input_1 = "";
    },

    addScreening() {
      this.$store.dispatch("changeLabel", {
        operation: "add",
        type: "spam_" + this.value_2,
        word: this.input_2,
      });
      this.value_2 = "";
      this.input_2 = "";
    },

    removeLabel(type, item) {
      this.$store.dispatch("changeLabel", {
        operation: "del",
        type: type,
        word: item,
      });
    },

    updateData() {
      this.$store.dispatch("updateData");
      ElMessage.success({
        message: "Data successfully updated!",
        type: "success",
      });
    },
  },
};
</script>

<style scoped>
#container {
  height: 90%;
  width: 80%;
  background-color: white;
  margin: auto;
}

.wrap {
  display: flex;
  padding-top: 8%;
}

.update {
  padding-top: 5%;
}

.labels {
  width: 100%;
  height: 20%;
  background-color: white;
  border: 1px solid silver;
  border-radius: 5%;
  margin: auto;
  text-align: left;
  padding-top: 10px;
}

.info {
  width: 100%;
  height: 25%;
  background-color: white;
  border: 1px solid silver;
  border-radius: 5%;
  margin: auto;
  text-align: left;
  padding-top: 5%;
}
</style>