<template>
  <el-card
    class="box-card"
    shadow="hover"
    :style="{ 'background-color': bgColor }"
    :body-style="{ 'text-align': 'left', 'font-size': 'small' }"
  >
    <div id="timestamp">{{ timeformat(TEXT.datetime) }}</div>
    <div id="container">
      <div id="wrapppp">
      <div id="left">
        <span class="author" :style="{ 'color': author }">{{ TEXT.author + ": " }}</span>

        <span class="author" :style="{ 'color': 'steelblue' }" v-for="a in AtList" :key="a">{{
          "@" + a + " "
        }}</span>

        {{ TEXT.Text }}

        <span class="tag" v-for="t in TagList" :key="t">{{
          "#" + t + " "
        }}</span>
      </div>
      </div>

      <div id="right">
        <div class="button-wrapper">
          <el-popover id="pop" placement="right" :width="180" trigger="click">
            <template #reference>
              <el-button
                class="button"
                icon="el-icon-refresh"
                size="mini"
                circle
              ></el-button>
            </template>
            <div>Relabel as:</div>
            <el-button
              v-if="TYPE != 1 && !clicked"
              class="button"
              size="mini"
              @click="change('to_relative')"
              >Relevant</el-button
            >
            <el-button
              v-if="TYPE != 2 && !clicked"
              class="button"
              size="mini"
              @click="change('to_chatter')"
              >Chatter</el-button
            >
            <el-button
              v-if="TYPE != 3 && !clicked"
              class="button"
              size="mini"
              @click="change('to_spam')"
              >Spam</el-button
            >
            <el-button
              v-if="clicked"
              class="button"
              size="mini"
              @click="change('')"
              >Reset</el-button
            >
          </el-popover>
        </div>

        <div
          class="sentiment"
          :style="{ 'background-color': color_sentiment }"
        ></div>
        <div
          class="sentiment"
          :style="{ 'background-color': color_subjectivity }"
        ></div>
      </div>
    </div>
  </el-card>
</template>

<script>
import * as d3 from "d3";

export default {
  name: "MessageCard",

  props: ["TEXT", "TYPE", "SENTIMENT", "Subjectivity", "ID"],

  data() {
    return {
      clicked: false,
      bgColor: this.TEXT.highlight==0 ? 'white' : 'ivory',
      author: this.TEXT.author == "Call Center" ? 'red' : 'steelblue'
    };
  },

  computed: {
    // ICON() {
    //   if (this.TYPE === 1) {
    //     return "el-icon-delete";
    //   } else {
    //     return "el-icon-star-off";
    //   }
    // },

    color_sentiment() {
      return d3.interpolateRdYlGn((this.SENTIMENT + 1) / 2);
    },

    color_subjectivity() {
      return d3.interpolateBlues((1 - this.Subjectivity) * 0.8);
    },

    TagList() {
      return eval(this.TEXT.Tags);
    },

    AtList() {
      return eval(this.TEXT.At);
    },
  },

  methods: {
    timeformat(d) {
      var b =
        Math.floor((d % 3600) / 60) < 10
          ? "0" + Math.floor((d % 3600) / 60)
          : Math.floor((d % 3600) / 60);
      var c = d % 60 < 10 ? "0" + (d % 60) : d % 60;
      return Math.floor(d / 3600) + ":" + b + ":" + c;
    },

    change(order) {
      // 已经点击过
      if (this.clicked) {
        this.clicked = false;
        this.bgColor = "white";
        this.$store.dispatch("changeText", {
          operation: "del",
          type: order,
          id: this.ID,
        });
      }
      // 未点击过
      else {
        this.clicked = true;
        this.bgColor = "whitesmoke";
        this.to_type = order;
        this.$store.dispatch("changeText", {
          operation: "add",
          type: order,
          id: this.ID,
        });
      }
    },
  },
};
</script>

<style scoped>
.box-card {
  width: 80%;
  margin: auto;
  margin-bottom: 5px;
  margin-top: 5px;
}

/* .author {
  color: steelblue;
} */

.tag {
  color: rgb(255, 127, 85);
}

#container {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

#left {
  width: 85%;
  word-break: keep-all;
  padding-top: 2%;
}

.sentiment {
  border-radius: 90%;
  width: 40%;
  height: 0;
  margin: auto;
  margin-top: 20%;
  padding-bottom: 40%;
  border: 1px solid lightsteelblue;
}

#timestamp {
  width: 95%;
  text-align: left;
  color: silver;
  font-size: x-small;
}
</style>
