<template>
  <el-container>
    <el-aside width="250px" style="background-color: whitesmoke">
      <Timeline />
    </el-aside>

    <el-container style="border: 1px solid #eee">
      <el-main style="background-color: aliceblue">
        <div id="main-wrapper">

          <div id="text-wrapper" style="height: 100vh; width: 100%">
            <Text />
          </div>

          <div
            id="graph-wrapper"
            style="
              height: 100vh;
              width: 100%;
              background-color: white;
              margin-bottom: 5%;
            "
          >
            <Graph />
          </div>

          <div
            id="big-wrapper"
            style="height: 100vh; width: 100%; background-color: white"
          >
            <div id="upper">
              <div
                id="map-wrapper"
                style="height: 60vh; width: 65%; background-color: white"
              >
                <Map />
              </div>

              <div
                id="message-wrapper"
                style="height: 60vh; width: 35%; background-color: white"
              >
                <MessageCard
                  v-for="text in relative"
                  :key="text.ID"
                  :TEXT="text"
                  :TYPE="1"
                  :SENTIMENT="text.polarity"
                  :Subjectivity="text.subjectivity"
                  :ID="text.ID"
                />
              </div>
            </div>

            <div id="down">
              <div
                id="race-wrapper"
                style="
                  height: 38vh;
                  width: 65%;
                  background-color: white;
                  overflow-x: hidden;
                  overflow-y: hidden;
                "
              >
                <Race />
              </div>

              <div
                id="cloud-wrapper"
                style="height: 38vh; width: 35%; background-color: white"
              >
                <WordCloud />
              </div>
            </div>
          </div>
        </div>
      </el-main>
    </el-container>

    <el-aside width="250px" style="background-color: whitesmoke">
      <Keyword />
    </el-aside>
  </el-container>
</template>

<script>
import Text from "../components/Text.vue";
import Timeline from "../components/Timeline.vue";
import Keyword from "../components/Keyword.vue";
import Map from "../components/Map.vue";
import Graph from "../components/Graph.vue";
import WordCloud from "../components/WordCloud.vue";
import Race from "../components/Race.vue";
import { mapGetters } from "vuex";
import MessageCard from "../components/MessageCard.vue";

export default {
  name: "Home",

  components: {
    Text,
    Timeline,
    Keyword,
    Map,
    Graph,
    WordCloud,
    Race,
    MessageCard,
  },

  computed: {
    ...mapGetters(["relative"]),
  },

  mounted() {},

  created() {
    // when the page is created, request global data
    this.$store.dispatch("getDataset");
    this.$store.dispatch("getMap");
    this.$store.dispatch("getGraph");
    this.$store.dispatch("getYuheng");
    this.$store.dispatch("getLocation");
  },
};
</script>

<style scoped>
.el-container {
  height: 100vh;
}

#text-wrapper {
  margin: auto;
}

#map-wrapper,
#cloud-wrapper,
#race-wrapper {
  /* margin: auto; */
  display: flex;
  /* border: 1px solid silver; */
  border-radius: 5px;
}

#message-wrapper {
  margin: auto;
  /* border: 1px solid silver; */
  border-radius: 5px;
  overflow-y: auto;
}

#graph-wrapper {
  margin: auto;
  display: flex;
  border: 1px solid silver;
  border-radius: 5px;
}

#big-wrapper {
  margin: auto;
  border: 1px solid silver;
  border-radius: 5px;
}

#upper,
#down {
  display: flex;
}

.box-card {
  width: 80%;
  margin: auto;
  margin-bottom: 5px;
  margin-top: 5px;
}
</style>