<template>
  <div id="mapDiv"></div>
</template>

<script>
import * as d3 from "d3";
import { mapGetters } from "vuex";

export default {
  name: "Map",

  computed: {
    ...mapGetters(["map", "points", "location", "focus"]),

    width() {
      return parseFloat(d3.select("#main-wrapper").style("width")) * 0.6;
    },

    height() {
      return (parseFloat(d3.select("#main-wrapper").style("height")) / 3) * 0.6;
    },
  },

  watch: {
    map() {
      this.drawLocation(this.location);
      this.drawMap(this.map);
    },

    focus() {
      this.drawRisk(this.focus);
    },

    points() {
      d3.selectAll(".points").remove();
      this.drawPoints(this.points);
    },
  },

  methods: {
    drawMap(data) {
      var height = this.height;

      var width = this.width;

      var svg = d3.select("#mapSVG");

      var projection = d3.geoMercator();

      var pathGenerator = d3.geoPath().projection(projection);

      var features = data.features;

      projection.fitExtent(
        [
          [0, 0],
          [this.width, this.height],
        ],
        data
      );

      svg
        .append("g")
        .attr("class", "map")
        .selectAll("path")
        .data(features)
        .enter()
        .append("path")
        .attr("d", pathGenerator)
        .attr("stroke", "#adadad")
        .attr("stroke-width", 1.5)
        .attr("fill", "white");
      // .on("mouseover", function () {
      //   d3.select(this).attr("stroke-width", 3).attr("stroke", "skyblue");
      // })
      // .on("mouseout", function () {
      //   d3.select(this).attr("stroke-width", 2).attr("stroke", "grey");
      // });

      var color = {
        mbdata: "#ffb53d",
        ccdata: "#e56656",
        "Important Locations": "steelblue",
        "Other Locations": "lightsteelblue",
      };

      svg
        .selectAll(".mydots")
        .data(Object.keys(color))
        .enter()
        .append("circle")
        .attr("cx", 0.73 * width)
        .attr("cy", function (d, i) {
          return 0.1 * height + i * 25;
        })
        .attr("r", 5)
        .style("fill", function (d) {
          return color[d];
        });

      // Add one dot in the legend for each name.
      svg
        .selectAll(".mylabels")
        .data(Object.keys(color))
        .enter()
        .append("text")
        .attr("x", 0.73 * width + 20)
        .attr("y", function (d, i) {
          return 0.1 * height + i * 25;
        })
        .style("fill", function (d) {
          return color[d];
        })
        .text(function (d) {
          if (d == "mbdata") {
            return "mbdata";
          } else if (d == "ccdata") {
            return "ccdata";
          } else if (d == "Important Locations") {
            return "Important Locations";
          } else {
            return "Other Locations";
          }
        })
        .attr("text-anchor", "left")
        .style("alignment-baseline", "middle");
    },

    drawPoints(points) {
      var div = d3
        .select("body")
        .append("div")
        .attr("class", "tooltip")
        .style("opacity", 0);

      var svg = d3.select("#mapSVG");

      var projection = d3.geoMercator();

      projection.fitExtent(
        [
          [0, 0],
          [this.width, this.height],
        ],
        this.map
      );
      svg
        .append("g")
        .attr("class", "mb")
        .selectAll("circle")
        .data(points)
        .enter()
        .append("circle")
        .attr("class", "points")
        .attr("r", 5)
        .attr("fill", function (d) {
          return d.type == "mbdata" ? "#ffb53d" : "#e56656";
        })
        .attr("opacity", 1)
        .attr("cx", (d) => projection([d.longitude, d.latitude])[0])
        .attr("cy", (d) => projection([d.longitude, d.latitude])[1])
        .on("mouseover", function (event, d) {
          div.transition().duration(200).style("opacity", 0.9);
          div
            .html(d.message)
            .style("left", event.pageX + "px")
            .style("top", event.pageY + "px");
        })
        .on("mouseout", function () {
          div.transition().duration(500).style("opacity", 0);
        });
    },

    drawLocation(locations) {
      var svg = d3
        .select("#mapDiv")
        .append("svg")
        .attr("id", "mapSVG")
        .attr("width", this.width)
        .attr("height", this.height);

      var div = d3
        .select("body")
        .append("div")
        .attr("class", "tooltip_location")
        .style("opacity", 0);

      var projection = d3.geoMercator();

      projection.fitExtent(
        [
          [0, 0],
          [this.width, this.height],
        ],
        this.map
      );

      svg
        .append("g")
        .attr("class", "loc")
        .selectAll("rect")
        .data(locations)
        .enter()
        .append("rect")
        .attr("class", "locations")
        .attr("width", (d) =>
          Math.abs(projection(d.point_2)[0] - projection(d.point_1)[0])
        )
        .attr("height", (d) =>
          Math.abs(projection(d.point_1)[1] - projection(d.point_2)[1])
        )
        .attr("x", (d) => projection(d.point_1)[0])
        .attr("y", (d) => projection(d.point_1)[1])
        .attr("fill", function (d) {
          if (d.type == "A") {
            return "steelblue";
          } else if (d.type == "B") {
            return "lightsteelblue";
          } else {
            return "lightsteelblue";
          }
        })
        .attr("opacity", 0.7)
        .on("mouseover", function (event, d) {
          div.transition().duration(200).style("opacity", 0.9);
          div
            .html(d.name)
            .style("left", event.pageX + "px")
            .style("top", event.pageY + "px");
        })
        .on("mouseout", function () {
          div.transition().duration(500).style("opacity", 0);
        });
    },

    drawRisk(data) {
      d3.select("#hex").remove();

      var div = d3
        .select("body")
        .append("div")
        .attr("class", "tooltip_risk")
        .style("opacity", 0);

      var Hexbin = require("../assets/hexbin.js").default;

      var projection = d3.geoMercator();

      projection.fitExtent(
        [
          [0, 0],
          [this.width, this.height],
        ],
        this.map
      );

      // 规定半径
      const R = this.width * 0.05;

      // 10分钟内的数据，即画图数据
      var mapdata = data;

      var hexbin = Hexbin()
        .radius(R)
        .extent([
          [0, 0],
          [this.width, this.height],
        ]);

      var hexbindata = [];
      mapdata.forEach(function (d) {
        hexbindata.push({
          [0]: projection([d.longitude, d.latitude])[0],
          [1]: projection([d.longitude, d.latitude])[1],
          ["Neg"]: -d.polarity,
          ["Message"]: d.message,
          ["Type"]: d.type,
        });
      });

      function risk(d) {
        let cccount = 0;
        let mbcount = 0;
        for (let i = 0; i < d.length; i++) {
          if (d[i].Type == "mbdata") mbcount += 1;
          else cccount += 1;
        }
        var colorresult = mbcount * d.polarity + cccount * 1.5;
        return colorresult;
      }

      var bins = hexbin(hexbindata).map(
        (d) => (
          (d.polarity = Math.exp(d3.mean(d, (d) => Math.max(0, d.Neg)))),
          (d.risk = risk(d)),
          d
        )
      );

      var color = d3.scaleSequential(d3.interpolateYlOrRd).domain([0, 10]);
      // var color = d3.scaleQuantile(d3.interpolateYlOrRd).domain([0, 30]);

      // var radius = d3.scaleSqrt(
      //   [0, d3.max(bins, (d) => d.length)],
      //   [0, hexbin.radius()]
      // );

      const svg = d3.select("#mapSVG");

      svg
        .append("g")
        .attr("id", "hex")
        .attr("stroke", "#000")
        .attr("stroke-opacity", 0.3)
        .selectAll("path")
        .data(bins)
        .join("path")
        // .attr("d", (d) => hexbin.hexagon(radius(d.length)))
        .attr("d", (d) => hexbin.hexagon(R))
        .attr("transform", (d) => `translate(${d.x},${d.y})`)
        .attr("fill", (d) => color(d.risk))
        .attr("opacity", 0.3)
        .append("title")
        .text(function (d) {
          let text = "";
          let mbc = 0;
          let ccc = 0;
          for (let i = 0; i < d.length; i++) {
            if (d[i].Type == "mbdata") {
              mbc += 1;
            }
            if (d[i].Type == "ccdata") {
              ccc += 1;
            }
            text += d[i].Message;
            text += "\n";
          }
          return `Risk level: ${d.risk.toFixed(2)}
Influence: ${(d.length / Object.keys(mapdata).length*100).toFixed(2)}% of population
Messages: ${d.length.toLocaleString()} (mbdata: ${mbc}, ccdata: ${ccc})
Negative emotion level: ${d.polarity.toFixed(2)}

${text}`;
        });
      // .on("mouseover", function (event, d) {
      //   div.transition().duration(200).style("opacity", 0.9);
      //   div
      //     .html(d.risk)
      //     .style("left", event.pageX + "px")
      //     .style("top", event.pageY + "px");
      // })
      // .on("mouseout", function () {
      //   div.transition().duration(500).style("opacity", 0);
      // });
    },
  },
};
</script>

<style scoped>
#mapDiv {
  margin: auto;
}
#info {
  position: absolute;
  background-color: silver;
  left: 80%;
  top: 0%;
  width: 20%;
  height: 20%;
}
</style>