<template>
  <div id="selection">
    <el-select
      v-model="value"
      placeholder="Render by:"
      size="mini"
      id="selector"
    >
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value"
        :disabled="item.disabled"
      >
      </el-option>
    </el-select>
  </div>
  <div id="container-timeline"></div>
</template>

<script>
import * as d3 from "d3";
import { mapGetters } from "vuex";

export default {
  name: "Timeline",

  data() {
    return {
      // width: 50,
      ticks: 120,
      value: "type",
      options: [
        {
          value: "type",
          label: "mbdata/ccdata",
        },
        {
          value: "category",
          label: "Relevant/Chatter/Spam",
        },
        {
          value: "event",
          label: "Events",
        },
        {
          value: "sentiment",
          label: "Negative Sentiment",
        },
      ],
    };
  },

  computed: {
    ...mapGetters([
      "dataset",
      "timeline_Type",
      "timeline_Category",
      "timeline_Event",
      "timeline_Sentiment",
      "events",
    ]),

    height() {
      return parseFloat(d3.select("#container-timeline").style("height"));
    },

    width() {
      return parseFloat(d3.select("#container-timeline").style("width")) / 3;
    },
  },

  watch: {
    dataset() {
      this.draw_axis();

      this.drawStream(this.timeline_Type, this.width * 2, this.height);

      var brush = d3
        .brushY()
        .extent([
          [0, 0],
          [this.width * 0.8, this.height],
        ])
        .on("end", (event) => this.brushed(event));
      d3.select("#svg_stack").call(brush);
    },

    events: {
      handler() {
        console.log("aha!");
        if (this.value == "event") {
          console.log("here!");
          this.draw_axis();

          this.drawStream(this.timeline_Event, this.width * 2, this.height);

          var brush = d3
            .brushY()
            .extent([
              [0, 0],
              [this.width * 0.8, this.height],
            ])
            .on("end", (event) => this.brushed(event));
          d3.select("#svg_stack").call(brush);
        }
      },
      deep: true,
    },

    value() {
      this.draw_axis();

      if (this.value == "type") {
        var data = this.timeline_Type;
      } else if (this.value == "category") {
        data = this.timeline_Category;
      } else if (this.value == "event") {
        data = this.timeline_Event;
      } else if (this.value == "sentiment") {
        data = this.timeline_Sentiment;
      }

      this.drawStream(data, this.width * 2, this.height);

      var brush = d3
        .brushY()
        .extent([
          [0, 0],
          [this.width * 0.8, this.height],
        ])
        .on("end", (event) => this.brushed(event));
      d3.select("#svg_stack").call(brush);
    },
  },

  methods: {
    draw_axis() {
      d3.select("#svg_stack").remove();

      var svg_stack = d3
        .select("#container-timeline")
        .append("svg")
        .attr("id", "svg_stack")
        .attr("width", this.width * 3)
        .attr("height", this.height);

      var y_stack = d3
        .scaleLinear()
        .domain([61200, 77684])
        .rangeRound([0, this.height]);

      var axis = svg_stack.append("g").attr("id", "chuizhi");

      axis
        .append("g")
        .attr("transform", "translate(" + this.width * 0.8 + ",0)")
        .call(
          d3
            .axisLeft(y_stack)
            .ticks(20)
            .tickFormat(function (d) {
              var b =
                Math.floor((d % 3600) / 60) < 10
                  ? "0" + Math.floor((d % 3600) / 60)
                  : Math.floor((d % 3600) / 60);
              var c = d % 60 < 10 ? "0" + (d % 60) : d % 60;
              return Math.floor(d / 3600) + ":" + b + ":" + c;
            })
        );
    },

    drawStream(data, width, height) {
      // append the svg object to the body of the page
      var svg = d3
        .select("#svg_stack")
        .append("g")
        .attr("transform", "translate(" + (width / 2) * 0.8 + ",0 )");

      var series = d3.stack().keys(Object.keys(data[0]).slice(1))(data);
      // .offset(d3.stackOffsetSilhouette)
      // .order(d3.stackOrderInsideOut)

      var keys = Object.keys(data[0]).slice(1); // 这里排除掉了time？
      // var formatDate = d3.utcFormat("%X %Y");
      var color = d3
        .scaleOrdinal()
        .domain(keys)
        .range([
          "#6390c5",
          "#ffb53d",
          "#e56656",
          "#6fc7c8",
          "#9dd2a7",
          "#ffb6b8",
          "#2ca6a8",
          "#d795b1",
          "#295168",
          "#8ac6d1",
          "#dda564",
          "#e48f6c",
        ]);

      svg
        .selectAll("mydots")
        .data(keys)
        .enter()
        .append("circle")
        .attr("cx", width / 1.5)
        .attr("cy", function (d, i) {
          return 10 + i * 15;
        }) // 100 is where the first dot appears. 25 is the distance between dots
        .attr("r", 3)
        .style("fill", function (d) {
          return color(d);
        });

      // Add one dot in the legend for each name.
      svg
        .selectAll("mylabels")
        .data(keys)
        .enter()
        .append("text")
        .attr("x", width / 1.5 + 7)
        .attr("y", function (d, i) {
          return 10 + i * 15;
        }) // 100 is where the first dot appears. 25 is the distance between dots
        .style("fill", function (d) {
          return color(d);
        })
        .text(function (d) {
          return d;
        })
        .attr("text-anchor", "left")
        .style("alignment-baseline", "middle");

      var x = d3
        .scaleLinear()
        .domain(d3.extent(data, (d) => d.no))
        .range([0, height]);

      // Add Y axis
      var y = d3
        .scaleLinear()
        .domain([
          d3.min(series, (d) => d3.min(d, (d) => d[0])),
          d3.max(series, (d) => d3.max(d, (d) => d[1])),
        ])
        .range([0, width]);

      // create a tooltip
      var Tooltip = svg
        .append("text")
        .attr("x", 100)
        .attr("y", 100)
        .style("opacity", 0)
        .style("font-size", 17);

      // Three function that change the tooltip when user hover / move / leave a cell
      var mouseover = function () {
        Tooltip.style("opacity", 1);
        d3.selectAll(".myArea").style("opacity", 0.3);
        d3.select(this).style("stroke", "#fff").style("opacity", 1);
      };

      const textArea = svg.append("g").attr("pointer-events", "none");
      textArea
        .append("text")
        .attr("font-size", 16)
        .attr("text-anchor", "middle");
      textArea
        .append("text")
        .attr("font-size", 16)
        .attr("text-anchor", "middle");

      var mousemove = function (d, i) {
        var grp = keys[i];
        Tooltip.text(grp);
      };

      var mouseleave = function () {
        Tooltip.style("opacity", 0);
        d3.selectAll(".myArea").style("opacity", 1).style("stroke", "none");
      };

      // Area generator
      var area = d3
        .area()
        .curve(d3.curveMonotoneY)
        .y((d) => x(d.data.no))
        .x0((d) => y(d[0]))
        .x1((d) => y(d[1]));

      svg
        .selectAll("mylayers")
        .data(series)
        .enter()
        .append("path")
        .attr("class", "myArea")
        .style("fill", function (d) {
          return color(d.key);
        })
        .attr("d", area)
        .on("mouseover", mouseover)
        .on("mousemove", mousemove)
        .on("mouseleave", mouseleave);
    },

    brushed(event) {
      if (event.selection !== null) {
        var a = Math.floor((event.selection[0] / this.height) * 16485) + 61200;
        var b = Math.floor((event.selection[1] / this.height) * 16485) + 61200;
        this.$store.dispatch("focus", { down: a, upper: b });
        this.$store.dispatch("setTimeRange", [a, b]);
      } else {
        this.$store.dispatch("reset");
      }
    },
  },
};
</script>

<style scoped>
#container-timeline {
  height: 93%;
  width: 90%;
  background-color: white;
  margin: auto;
  padding-top: 2%;
  padding-bottom: 2%;
  border-radius: 10px;
  border: 1px solid silver;
}

#selection {
  width: 50%;
  margin: auto;
}
</style>
